@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
body{
  font-family: 'poppins';
  text-align: center;
  font-size: 30px;
}
.rows{
  display: flex;
}
.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  position: relative;
}
.sq{
  border: 1px solid black;
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 800;
}
.heading{
  margin-bottom:100px;
}

.butt{
  margin-top: 20px;
  width: 100px;
  height: 50px;
}
